import React, { forwardRef, useRef } from 'react';
import {
    AriaPopoverProps,
    AriaSelectOptions,
    HiddenSelect,
    useInteractOutside,
    useSelect,
} from 'react-aria';
import { useSelectState } from 'react-stately';
import { useObjectRef } from '@react-aria/utils';
import type { CollectionChildren } from '@react-types/shared';
import classNames from 'classnames';

import SvgCaretDown from '../Icons/CaretDown';
import Popover from '../Popover/Popover';
import SelectListBox from '../SelectListBox/SelectListBox';
import { Stack } from '../Stack';
import type { DescriptionSeverity } from '../types';
import { Label, Text } from '../Typography';

import SelectButton from './SelectButton';

import styles from './Select.module.scss';

export interface SelectProps
    extends Omit<AriaSelectOptions<object>, 'errorMessage'> {
    children?: CollectionChildren<object>;
    errorMessage?: string;
    descriptionSeverity?: DescriptionSeverity;
    labelSuffix?: string;
    tooltipTrigger?: React.ReactNode;
    isNonModal?: AriaPopoverProps['isNonModal'];
}

const Select = forwardRef<HTMLButtonElement, SelectProps>((props, ref) => {
    const forwardedRef = useObjectRef(ref);
    const fallbackRef = useRef(null);
    const triggerRef = forwardedRef || fallbackRef;
    const portalRef = useRef(null);

    const state = useSelectState(props);
    const {
        labelProps,
        triggerProps,
        valueProps,
        menuProps,
        descriptionProps,
        errorMessageProps,
        isInvalid,
        validationErrors,
    } = useSelect(props, state, triggerRef);

    const { isOpen, selectedItem } = state;
    const {
        label,
        name,
        placeholder,
        description,
        validationBehavior,
        errorMessage: customErrorMessage,
        descriptionSeverity = 'regular',
        labelSuffix,
        tooltipTrigger,
        isNonModal = false,
    } = props;

    useInteractOutside({
        ref: portalRef,
        onInteractOutside: state.close,
    });

    const classList = classNames(styles.select, {
        [styles['select--active']]: isOpen,
        [styles['select--invalid']]: isInvalid,
    });

    const descriptionClassList = classNames({
        [styles['messages__description--regular']]:
            descriptionSeverity === 'regular',
        [styles['messages__description--warning']]:
            descriptionSeverity === 'warning',
    });

    // Allow only native error messages with native validation behaviour
    const errorMessage =
        validationBehavior === 'native'
            ? validationErrors.join(' ')
            : customErrorMessage;

    const hasMessages = errorMessage || description;

    const title = Array.isArray(selectedItem?.rendered)
        ? selectedItem.rendered[0]
        : selectedItem?.rendered;

    return (
        <div ref={portalRef} className={styles.base}>
            <Stack>
                {label && (
                    <div className={styles.label__container}>
                        <Label
                            weight="600"
                            className={styles.label}
                            {...labelProps}
                        >
                            {label}
                            {labelSuffix && (
                                <>
                                    {/* Use an extra element as separator so everything stays inline and no extra margin on either side needed, which doesn't look right when text starts wrapping */}
                                    <span
                                        className={
                                            styles['label__suffix-separator']
                                        }
                                    ></span>
                                    <Text
                                        variant="line"
                                        italic
                                        size="small"
                                        className={styles.label__suffix}
                                    >
                                        {labelSuffix}
                                    </Text>
                                </>
                            )}
                        </Label>
                        {tooltipTrigger}
                    </div>
                )}

                <HiddenSelect
                    state={state}
                    triggerRef={triggerRef}
                    label={label}
                    name={name}
                />

                <SelectButton
                    {...triggerProps}
                    buttonRef={
                        triggerRef as React.MutableRefObject<HTMLButtonElement>
                    }
                    className={classList}
                >
                    <span {...valueProps}>
                        {selectedItem ? (
                            title
                        ) : (
                            <span className={styles.select__placeholder}>
                                {placeholder}
                            </span>
                        )}
                    </span>
                    <SvgCaretDown
                        className={styles.select__icon}
                        aria-hidden="true"
                    />
                </SelectButton>

                {hasMessages && (
                    <Stack>
                        {/* Show custom error message only when isInvalid */}
                        {isInvalid && errorMessage && (
                            <div
                                {...errorMessageProps}
                                className={styles.messages__error}
                            >
                                <Text variant="line" size="small">
                                    {errorMessage}
                                </Text>
                            </div>
                        )}
                        {description && (
                            <div
                                {...descriptionProps}
                                className={descriptionClassList}
                            >
                                <Text variant="line" size="small">
                                    {description}
                                </Text>
                            </div>
                        )}
                    </Stack>
                )}
            </Stack>

            {isOpen && (
                <Popover
                    state={state}
                    triggerRef={triggerRef}
                    portalContainer={portalRef.current}
                    offset={6}
                    className={styles.popover}
                    placement="bottom start"
                    isNonModal={isNonModal}
                >
                    <SelectListBox {...menuProps} state={state} />
                </Popover>
            )}
        </div>
    );
});

Select.displayName = 'Select';

export default Select;
